@tailwind base;
@tailwind components;
@tailwind utilities;

$admin-primary: #fff;
$admin-secondary:#fff;
$admin-background: #0E4C92;
$admin-text:#c0a9a9;


$student-primary: #550393;
$student-secondary:#47ACD2;
$student-background: #47ACD2;
$student-text:#c0a9a9;
$student-border:#F1D9FF;

$prof-primary:#47ACD2;
$prof-secondary:#fff;
$prof-background:#550393;
$prof-text:#fff;


body{
    height: 100%;
}

div {
    width: 100%;
}

.student-primary{
    color: $student-primary;
}

.student-secondary{
    color: $student-secondary;
}

.prof-primary{
    color: $prof-primary ;
}

.admin-primary{
    color: $admin-primary;
}

.student-background {
    background-color: $student-background;
}

.prof-background{
    background-color: $prof-background;
}

.admin-background {
    background-color: $admin-background;
}

.student-border {
    border:1px solid #F1D9FF;
    border-radius: 31.1px;
}

.student-border-selection {
    border-color: $student-primary;
}

.student-dashboard-title {
    font-family: Outfit;
    font-size: 43px;
    font-weight: 400;
    line-height: 54px;
    letter-spacing: 0em;
    text-align: left;
}


.cadran-eleve-solde {
    padding-top: 57px;
    padding-bottom: 57px;
    margin-top: 25px;
    margin-left: 15%;
    
}

.bg-color-cadran-student {
    background-color: #FF7D65;
    
}

.font-solde-title {
    font-family: Outfit;
    font-size: 41px;
    font-weight: 400;
    line-height: 52px;
    letter-spacing: 0em;
    text-align: left;
    color: #565973;
}

.font-solde-text {
    font-family: Poppins;
    font-size: 43px;
    font-weight: 500;
    line-height: 65px;
    letter-spacing: 0em;

}

.call-div{
    width: 100%;
}